.btn-list {
  height: 89vh;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 0;
  list-style: none;
  display: flex;
}

.btn {
  width: 150px;
  height: 70px;
  cursor: pointer;
  color: #fff;
  background-color: #04c279;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 50px;
  font-family: Roboto;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
}

.btn:disabled {
  color: #beadad;
  background-color: gray;
}

